import { Component, OnInit } from '@angular/core';
import {SocketService} from '../../theme/services/socketService/socket.service'
import {PieChartService} from './pieChart/pieChart.service'

@Component({
  selector: 'app-stationlist',
  templateUrl: './stationlist.component.html',
  styleUrls: ['./stationlist.component.scss']
})
export class StationlistComponent implements OnInit {
  public data:any = {
    humi: 0,
    light: 0,
    soil: 0,
    temp: 0,
    time: 0,
    user: "",
    mid: ""    
  }

  public relay01:boolean= false;
  public relay02:boolean= false;
  public relay03:boolean= false;

  constructor(private io:SocketService,private pi:PieChartService) { 
    this.io.socket.emit('join-wa', { 'user': 'admin'})
    this.io.socket.on('connect', ()=>this.connect());
    this.io.socket.on('SENSORS_DATA', (data)=>this.Sensor(data));
  }

  ngOnInit() {

  }

  private connect(): void {
    console.log("[SocketIO] Connected");
    this.io.socket.emit('join-wa', {'user': 'admin'});
  }
  
  private Sensor(data): void{
    // console.log(data);
    this.data.humi = data.humi;
    this.data.light = data.light;
    this.data.soil = data.soil;
    this.data.temp = data.temp;
    this.data.time = data.time;
    this.data.user = data.user;
    this.data.mid = data.mid;
    this.data.vpd = data.vpd;
    // console.log(this.data);
  }

  private onChange($evnt,relay){
    
    var r = {
      'relay': relay,
      'evnt': $evnt,
      'user': 'admin'
    }
    this.io.socket.emit("RELAYS", r);
  }

}



